@import url('https://fonts.googleapis.com/css?family=Inter:400|Inter:400');

html {
	font-size: 100%;
} /*16px*/

body {
	background-color: #fefefe;
	font-family: 'Inter', sans-serif;
	font-weight: 400;
	line-height: 1.65;
	color: #333;
}

p {
	margin-bottom: 1.15rem;
}

h1,
h2,
h3,
h4,
h5 {
	margin: 2.75rem 0 1.05rem;
	font-family: 'Inter', sans-serif;
	font-weight: 400;
	line-height: 1.15;
}

h1 {
	margin-top: 0;
	font-size: 2.488em;
}

h2 {
	font-size: 2.074em;
}

h3 {
	font-size: 1.728em;
}

h4 {
	font-size: 1.44em;
}

h5 {
	font-size: 1.2em;
}

small,
.text-small {
	font-size: 0.7em;
}

* {
	box-sizing: border-box;
}

main {
	padding: 2rem;
	display: grid;
	justify-items: left;
	width: 100%;
}
